@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,400i,700&display=swap");
/* Base
–––––––––––––––––––––––––––––––––––––––––––––––––– */
body {
  color: #22252B;
  font-family: "Lato", sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: #87898C;
}

/* Header
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Header {
  background-color: white;
  border-bottom: 1px solid #EBECEE;
  box-shadow: none;
  color: #22252B;
  height: 60px;
  justify-content: space-between;
  padding-right: 20px;
  position: fixed;
  width: 100%;
}

.Header > div {
  align-items: center;
  display: flex;
}

.Header a {
  font-size: 21px;
  font-weight: 700;
}

[dir=ltr] .Header-button, [dir=rtl] .Header-button {
  border: 0 none;
  border-right: 1px solid #EBECEE;
  transition: background-color 0.15s ease-out;
}

.Header-navToggle {
  height: 60px;
  width: 60px;
}

.Header-title {
  padding: 0 20px;
  transition: color 0.15s ease-out;
}

.Header-title:hover {
  color: #87898C;
}

.Header-Utils a {
  margin-left: 20px;
}

.Header-Utils a svg path {
  transition: fill 0.15s ease-out;
}

.Header-Utils a:hover svg path {
  fill: #87898C;
}

/* Frame
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Frame-body {
  height: calc(100% - 60px);
  margin-top: 60px;
}

.Frame-panel--main {
  width: calc(100% - 280px);
}

.Frame-panel--sidebar {
  background-color: #FAFAFA;
  border-inline-end: none;
  padding: 40px 0 20px;
  min-width: 280px;
}

[dir="ltr"] .Frame-panel--sidebar {
  border-right: 1px solid #EBECEE;
}

/* Sidebar
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Navigation-search {
  padding: 0 0 20px;
}

.Tree-title, .Search-label {
  background-color: whitesmoke;
  border-top: 1px solid #EBECEE;
  border-bottom: 1px solid #EBECEE;
  color: rgba(34, 37, 43, 0.37);
  display: block;
  font-family: "Lato", sans-serif;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: .75px;
  margin-bottom: 0;
  padding: 10px 20px 8px;
}

.Search-input {
  border-radius: 0;
  border: none;
  font-size: 12px;
  padding: 10px 20px 8px;
}

[dir="ltr"] .Tree-title {
  padding-left: 20px;
}

[dir="rtl"] .Tree-title {
  padding-right: 20px;
}

.Tree-items.Tree-depth-1 {
  padding-top: 5px;
}

.Tree-item {
  color: rgba(34, 37, 43, 0.87);
  font-family: inherit;
  font-size: 14px;
}

.Tree-aside {
  color: rgba(34, 37, 43, 0.54);
  font-family: inherit;
  font-size: 12px;
}

[dir="ltr"] .Tree-entityLink, [dir="rtl"] .Tree-entityLink {
  padding: 5px 20px !important;
}

.is-current .Tree-entityLink {
  background-color: white;
}

.Navigation-group + .Navigation-group {
  margin-top: 10px;
}

[dir="ltr"] .Tree-depth-3 > .Tree-collectionLabel {
  padding: 6px 20px 4px !important;
}

[dir="ltr"] .Tree-depth-4 > .Tree-collectionLabel {
  padding: 6px 20px 4px !important;
}

[dir="rtl"] .Tree-depth-3 > .Tree-collectionLabel {
  padding: 6px 0 4px !important;
}

[dir="rtl"] .Tree-depth-4 > .Tree-collectionLabel {
  padding: 6px 0 4px !important;
}

[dir="ltr"] .Tree-collectionItems .Tree-entityLink {
  margin-left: 0 !important;
  padding: 7px 20px !important;
}

[dir="rtl"] .Tree-collectionItems .Tree-entityLink {
  margin-right: 0 !important;
  padding: 7px 0 !important;
}

.Tree-collectionLabel {
  color: rgba(34, 37, 43, 0.87);
  font-family: inherit;
  font-size: 14px;
  padding: 5px 20px !important;
}

.Tree-collectionLabel span:before {
  background-image: url("img/icon-navigation-opened.svg") !important;
  background-size: auto;
  opacity: 1;
  top: 5px;
}

.Tree-collectionLabel:hover {
  background-color: transparent;
  color: #87898C !important;
  transition: all 0.15s ease-out;
}

[dir="ltr"] .Tree-collectionLabel span {
  margin-left: 0 !important;
  padding-left: 0 !important;
}

[dir="ltr"] .Tree-collectionLabel span:before {
  left: initial !important;
  right: 0 !important;
}

[dir="rtl"] .Tree-collectionLabel span {
  margin-right: 0 !important;
  padding-right: 0 !important;
}

[dir="rtl"] .Tree-collectionLabel span:before {
  right: initial !important;
  left: 0 !important;
}

.Tree-collection.is-closed .Tree-collectionLabel span:before {
  background-image: url("img/icon-navigation-closed.svg") !important;
}

/* Pen
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Pen {
  overflow: auto;
  position: relative;
}

.Pen-top {
  display: flex;
  margin: 40px 80px;
}

.Pen-top .Prose {
  max-width: 720px;
}

.Pen-info {
  border: none;
  position: absolute;
  top: calc(100vh - 280px);
  left: 0;
  margin: 0;
  width: 100%;
}

.Browser {
  position: relative;
}

.Browser-controls {
  background-color: white;
  border-top: 1px solid #EBECEE;
  border-bottom: 1px solid #EBECEE;
  box-shadow: none;
}

.Browser-tab {
  flex: initial;
}

.Browser-tab a {
  border-right: 1px solid #EBECEE;
  border-bottom: 0;
  color: rgba(34, 37, 43, 0.87);
  font-family: "Lato", sans-serif;
  font-size: 14px;
  font-weight: 400;
  padding: 8px 20px 7px;
}

.Browser-tab a:hover {
  background-color: #FAFAFA;
}

.Browser-tab a span {
  color: rgba(34, 37, 43, 0.37);
  font-size: 10px;
}

.Browser-tab.is-active a {
  background-color: #FAFAFA;
  font-weight: 400;
}

.Browser-panel {
  background-color: #FAFAFA;
  min-height: 240px;
  padding: 20px;
}

.Browser-panel .Code {
  background-color: #FAFAFA;
}

.Browser-panel .Code pre {
  background-color: #FAFAFA;
}

.Pen-header {
  align-items: center;
  background-color: whitesmoke;
  border-top: 1px solid #EBECEE;
  border-bottom: 1px solid #EBECEE;
  justify-content: space-between;
  max-height: initial;
  padding: 10px 20px;
}

.Pen-header .Status {
  display: inline-block;
  margin-left: 40px !important;
  padding-left: 0 !important;
  vertical-align: bottom;
}

.Pen-title {
  color: #22252B;
  font-family: "Lato", sans-serif;
  font-size: 24px;
  font-weight: 700;
}

.Pen-previewLink {
  background-color: transparent;
  border: 1px solid #87898C;
  color: #87898C;
  padding: 7px 20px;
}

.Pen-previewLink svg {
  margin-left: 10px;
  opacity: 1;
}

.Pen-previewLink:hover {
  background-color: whitesmoke;
}

.Pen-preview {
  background-color: white;
  border: none;
  margin: 20px;
  min-height: 0;
  overflow-x: hidden;
}

/* Prose
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Prose {
  flex: 1 1 auto;
  font-family: "Lato", sans-serif;
  line-height: 1.625;
  max-width: initial;
  width: 50%;
}

.Prose p, .Prose > *:not(:last-child) {
  margin-bottom: 20px;
}

.Prose a {
  color: #0AD3FF;
}

.Prose a:hover {
  color: #87898C;
}

.Prose h1, .Prose h2, .Prose h3 {
  color: #22252B;
  font-family: "Lato", sans-serif;
  font-weight: 700;
}

.Prose h1 {
  border-bottom: 1px solid #EBECEE;
  font-size: 40px;
  letter-spacing: 0;
  margin-bottom: 40px !important;
  padding-bottom: 10px;
}

.Prose h2 {
  font-size: 24px;
  margin: 30px 0 20px;
}

.Prose h3 {
  font-size: 16px;
  margin: 30px 0 20px;
}

.Prose *:not(hr) + h2 {
  margin-top: 30px;
}

.Prose ul, .Prose ol, .Prose dl {
  padding-left: 20px !important;
}

.Prose dd {
  margin-bottom: 20px;
}

.Prose table {
  font-size: 16px;
  margin: 20px 0;
}

.Prose table th {
  background-color: transparent;
  border-bottom: 1px solid rgba(34, 37, 43, 0.54);
  padding: 12px 20px;
}

.Prose table tr {
  border-bottom: 1px solid #EBECEE;
}

.Prose table tr:nth-child(even) {
  background-color: transparent;
}

.Prose table td {
  padding: 12px 20px;
}

.Prose code pre {
  margin: 20px 0;
  padding: 15px 20px;
}

/* Status
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Status-label {
  border: 1px solid transparent;
  border-radius: 15px;
  font-family: inherit;
  letter-spacing: .75px;
  padding: 4px 20px;
}

/* Documentation
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Document {
  margin: 40px 80px;
  overflow: auto;
  padding: 0;
}

.Document .Prose {
  width: auto;
}

.Document-header {
  border-bottom: 1px solid #EBECEE;
  margin-bottom: 40px;
  padding-bottom: 10px;
}

.Document-title {
  color: #22252B;
  font-family: "Lato", sans-serif;
  font-size: 40px;
  font-weight: 700;
  letter-spacing: 0;
}

.overview-statuses {
  display: flex;
}

.overview-statuses .overview-status {
  width: 50%;
}

.overview-statuses .overview-status:first-child {
  margin-right: 20px;
}

.overview-statuses .overview-status:last-child {
  margin-left: 20px;
}

.swatches {
  display: flex;
  flex-wrap: wrap;
}

.swatches .color-swatch {
  border: 1px solid #EBECEE;
  border-radius: 25px;
  margin: 0 20px 20px 0;
  padding: 20px;
  width: 250px;
}

.c-white {
  color: white;
}

